@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

nav.nav-pc {
  width: 100vw;
  height: 50px;
  display: flex;
  justify-content: space-between;
  padding: 0 calc(15vw / 2);
  box-shadow: 0px 0px 7px 0px rgb(87, 87, 87);
  align-items: center;
  overflow-y: hidden;
  position: fixed;
  background-color: white;
  z-index: 1;
  top: 0px;
}
nav.nav-pc ul.list-pc {
  display: flex;
  gap: 20px;
  list-style-type: none;
}
nav.nav-pc ul.list-pc a {
  color: #101061;
}
nav.nav-pc ul.list-pc a::after {
  content: "";
  position: relative;
  display: block;
  left: 0;
  right: 0;
  width: 0px;
  background-color: #006196;
  border-radius: 10px;
  height: 4px;
  transition: all 0.2s ease-in-out;
}
nav.nav-pc ul.list-pc a.active {
  color: #006196;
}
nav.nav-pc ul.list-pc a.active::after {
  width: 100%;
}
.logo-pc {
  width: 90px;
  height: 50px;
}
.logo-pc img {
  width: 100%;
  position: relative;
  left: -25px;
}
